<template>
  <div class="essay-editor">
    <el-form :model="form" label-width="80px">
      <el-form-item label="题目">
        <el-input v-model="form.title" placeholder="请输入作文题目（可选）" />
      </el-form-item>
      <el-form-item label="类型">
        <el-select
          v-model="form.prompt"
          filterable
          allow-create
          clearable
          default-first-option
          placeholder="请选择或输入作文类型（可选）"
          style="width: 100%"
        >
          <el-option label="议论文" value="议论文" />
          <el-option label="记叙文" value="记叙文" />
          <el-option label="应用文" value="应用文" />
          <el-option label="说明文" value="说明文" />
          <el-option label="散文" value="散文" />
          <el-option label="其他" value="其他" />
        </el-select>
      </el-form-item>
      <el-form-item label="内容">
        <!-- 无文件时：只显示上传区域 -->
        <el-upload
          v-if="!fileList.length"
          class="essay-upload"
          v-model:file-list="fileList"
          :auto-upload="false"
          :limit="1"
          :show-file-list="false"
          :on-change="handleFileChange"
          accept=".docx,.doc"
          drag
        >
          <el-icon class="el-icon--upload"><upload-filled /></el-icon>
          <div class="el-upload__text">将Word文件拖到此处，或<em>点击上传</em></div>
          <template #tip>
            <div class="el-upload__tip">支持Word文档，文件大小不超过1MB</div>
          </template>
        </el-upload>
        <!-- 有文件时：只显示文件列表 -->
        <div v-else class="essay-file-list">
          <div class="essay-file-item">
            <el-icon><document /></el-icon>
            <span class="essay-file-name">{{ fileList[0]?.name }}</span>
            <el-button type="primary" link @click="handleFileRemove">删除</el-button>
          </div>
        </div>
        <el-input
          v-model="form.content_text"
          type="textarea"
          :rows="15"
          placeholder="或直接在此输入作文内容..."
          style="margin-top: var(--spacing-md)"
        />
      </el-form-item>
    </el-form>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive } from 'vue'
import { UploadFilled, Document } from '@element-plus/icons-vue'
import { ElMessage } from 'element-plus'
import type { UploadFile } from 'element-plus'
import { extractTextFromFile } from '@/utils/fileParse'

const emit = defineEmits<{
  (e: 'update:content', value: string): void
  (e: 'update:file', value: File | null): void
}>()

const form = reactive({
  title: '',
  prompt: '',
  content_text: '',
})

const fileList = ref<UploadFile[]>([])

async function handleFileChange(file: UploadFile) {
  if (!file.raw) return
  if (file.raw.size > 1024 * 1024) {
    ElMessage.error('文件大小不能超过1MB')
    fileList.value = []
    return
  }
  try {
    const text = await extractTextFromFile(file.raw)
    form.content_text = text
    emit('update:content', text)
    emit('update:file', file.raw)
  } catch {
    ElMessage.error('Word 解析失败，请确认文件格式')
    fileList.value = []
  }
}

function handleFileRemove() {
  fileList.value = []
  form.content_text = ''
  emit('update:file', null)
}

defineExpose({
  form,
  clear: () => {
    form.title = ''
    form.prompt = ''
    form.content_text = ''
    fileList.value = []
  },
})
</script>

<style scoped>
.essay-editor {
  width: 100%;
}

/* 有文件时不显示上传拖拽区，只显示文件列表 */
.essay-file-list {
  border: 1px dashed var(--el-border-color);
  border-radius: var(--el-border-radius-base);
  padding: var(--spacing-md, 12px);
  background: var(--el-fill-color-light);
}

.essay-file-item {
  display: flex;
  align-items: center;
  gap: 8px;
}

.essay-file-name {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* 小屏时 label 改为 40px（与全站窄屏断点 768px 一致） */
@media (max-width: 768px) {
  .essay-editor :deep(.el-form-item__label) {
    width: 40px !important;
  }
}
</style>
