<template>
  <div class="correction-page">
    <div class="container">
      <el-card shadow="never">
        <template #header>
          <div class="page-header">
            <h2 class="header-font">作文批改</h2>
            <div
              v-if="
                !essayStore.currentEssay ||
                (essayStore.currentEssay.status !== 'done' &&
                  essayStore.currentEssay.status !== 'processing')
              "
              class="header-actions"
            >
              <el-button v-if="authStore.isMember" @click="showDrafts = true"> 草稿列表 </el-button>
            </div>
          </div>
        </template>

        <div
          v-if="
            !essayStore.currentEssay ||
            (essayStore.currentEssay.status !== 'done' &&
              essayStore.currentEssay.status !== 'processing')
          "
        >
          <EssayEditor ref="editorRef" />
          <div class="submit-section">
            <el-alert
              v-if="!authStore.isAuthenticated"
              class="tip-alert"
              title="提示"
              type="warning"
              :closable="false"
              show-icon
            >
              未登录用户无法批改作文，<el-link type="primary" @click="$router.push('/login')">
                立即登录
              </el-link>
            </el-alert>
            <div v-if="authStore.isAuthenticated" class="submit-actions">
              <template v-if="authStore.isMember">
                <el-button
                  type="primary"
                  size="large"
                  :loading="essayStore.loading"
                  @click="handleSubmit()"
                >
                  提交批改
                </el-button>
              </template>
              <template v-else>
                <el-button
                  type="primary"
                  size="large"
                  :loading="essayStore.loading"
                  @click="handleSubmit('ink_drops')"
                >
                  使用 100 墨滴批改
                </el-button>
                <el-button
                  type="primary"
                  size="large"
                  plain
                  :loading="essayStore.loading"
                  @click="handleSubmit('quota')"
                >
                  使用 1 次次卡批改
                </el-button>
              </template>
              <el-button v-if="authStore.isAuthenticated" size="large" @click="handleSaveDraft">
                保存草稿
              </el-button>
            </div>
          </div>
        </div>

        <div v-else-if="essayStore.currentEssay && essayStore.currentEssay.status === 'processing'">
          <el-result icon="loading" title="正在批改中...">
            <template #sub-title>
              <p>AI正在分析您的作文，请稍候</p>
            </template>
          </el-result>
        </div>

        <div v-else-if="essayStore.currentCorrection">
          <div class="result-header">
            <el-button @click="handleReset">重新批改</el-button>
            <template v-if="authStore.isAuthenticated">
              <el-button
                v-if="!essayStore.currentEssay?.is_favorited"
                type="primary"
                :loading="favoriteLoading"
                @click="handleFavorite"
              >
                收藏
              </el-button>
              <el-button v-else :loading="unfavoriteLoading" @click="handleUnfavorite">
                取消收藏
              </el-button>
            </template>
          </div>
          <CorrectionResult :correction="essayStore.currentCorrection" />
        </div>
      </el-card>
    </div>

    <!-- 草稿列表对话框 -->
    <el-dialog v-model="showDrafts" title="草稿列表" width="600px">
      <el-table :data="essayStore.drafts" class="drafts-table" style="width: 100%">
        <el-table-column prop="title" label="标题" />
        <el-table-column prop="updated_at" label="更新时间" width="180">
          <template #default="{ row }">
            {{ new Date(row.updated_at).toLocaleString() }}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="140" align="right">
          <template #default="{ row }">
            <div class="draft-op-row">
              <el-button type="primary" text @click="loadDraft(row)">加载</el-button>
              <el-button type="danger" text @click="deleteDraft(row.id)">删除</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div v-if="essayStore.drafts.length === 0" class="drafts-empty-state">
        <el-empty description="暂无草稿">
          <el-button type="primary" @click="showDrafts = false">开始编辑</el-button>
        </el-empty>
      </div>
    </el-dialog>

    <!-- 非会员点击保存草稿时提示 -->
    <el-dialog
      v-model="showDraftMemberTip"
      title="开通会员"
      width="400px"
      class="app-dialog"
      align-center
    >
      <p class="draft-member-tip-p">
        <el-link type="primary" @click="closeDraftTipAndGo('/profile?tab=membership')">
          开通会员
        </el-link>
        可保存草稿，可前往
        <el-link type="primary" @click="closeDraftTipAndGo('/essay/history')"> 历史记录 </el-link>
        查看草稿。
      </p>
      <template #footer>
        <el-button @click="showDraftMemberTip = false">关闭</el-button>
        <el-button type="primary" @click="closeDraftTipAndGo('/profile?tab=membership')">
          去开通
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, nextTick, watch, h } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useAuthStore } from '@/stores/auth'
import { useEssayStore } from '@/stores/essay'
import { unfavoriteEssay } from '@/api/essay'
import EssayEditor from '@/components/Essay/EssayEditor.vue'
import CorrectionResult from '@/components/Essay/CorrectionResult.vue'
import { ElMessage, ElMessageBox } from 'element-plus'

const route = useRoute()
const router = useRouter()
const authStore = useAuthStore()
const essayStore = useEssayStore()

const canUseInkDrops = computed(() => (authStore.user?.ink_drops ?? 0) >= 100)
const canUseQuota = computed(() => (authStore.user?.membership?.quota_remaining ?? 0) > 0)
const editorRef = ref<InstanceType<typeof EssayEditor>>()
const showDrafts = ref(false)
const showDraftMemberTip = ref(false)

function closeDraftTipAndGo(path: string) {
  showDraftMemberTip.value = false
  router.push(path)
}
/** 重新批改时暂存作文内容，编辑器挂载后回填 */
const pendingEditorContent = ref<{ title: string; prompt: string; content_text: string } | null>(
  null
)

async function loadByRoute() {
  const essayId = route.params.id as string
  if (essayId) {
    await essayStore.fetchEssay(Number(essayId))
  } else {
    essayStore.clearCurrent()
  }
}

onMounted(async () => {
  await loadByRoute()
  if (authStore.isAuthenticated) {
    await essayStore.fetchDrafts()
  }
  const draftId = route.query.draftId as string
  if (draftId) {
    nextTick(() => {
      const draft = essayStore.drafts.find((d) => d.id === Number(draftId))
      if (draft && editorRef.value) {
        editorRef.value.form.title = draft.title ?? ''
        editorRef.value.form.content_text = draft.content_text ?? ''
      }
      router.replace({ path: route.path, query: {} })
    })
  }
})

// 同一组件复用：从 /essay/correction 进入 /essay/:id 时需根据 id 拉取批改结果
watch(
  () => route.params.id,
  async () => {
    await loadByRoute()
  }
)

async function handleSubmit(payType?: 'ink_drops' | 'quota') {
  if (!editorRef.value) return

  const { form } = editorRef.value
  if (!form.content_text.trim()) {
    ElMessage.warning('请输入作文内容')
    return
  }

  // 非会员：余额或次数不足时拦截并弹窗提示
  if (!authStore.isMember && payType) {
    if (payType === 'ink_drops' && !canUseInkDrops.value) {
      await ElMessageBox.alert(
        h('p', null, [
          '请 ',
          h(
            'span',
            {
              class: 'ink-shortage-link',
              onClick: () => {
                ElMessageBox.close()
                router.push('/profile?tab=membership')
              },
            },
            '升级会员'
          ),
          ' 获得更多服务',
        ]),
        '墨滴不足',
        { confirmButtonText: '确定', customClass: 'app-dialog' }
      )
      return
    }
    if (payType === 'quota' && !canUseQuota.value) {
      const goMembership = () => {
        ElMessageBox.close()
        router.push('/profile?tab=membership')
      }
      await ElMessageBox.alert(
        h('p', null, [
          '请 ',
          h('span', { class: 'ink-shortage-link', onClick: goMembership }, '兑换次卡'),
          ' 或 ',
          h('span', { class: 'ink-shortage-link', onClick: goMembership }, '升级会员'),
          ' 以获得更多服务',
        ]),
        '次数不足',
        { confirmButtonText: '确定', customClass: 'app-dialog' }
      )
      return
    }
  }

  const payload: Parameters<typeof essayStore.submitEssay>[0] = {
    title: form.title,
    prompt: form.prompt,
    content_text: form.content_text,
    source_type: 'text',
  }
  if (payType) payload.pay_type = payType

  try {
    await essayStore.submitEssay(payload)
    // 刷新用户信息（墨滴/次卡已扣减，保证各处显示正确）
    await authStore.fetchUser()

    // 轮询检查批改结果；fetchEssay 在 status===done 时已拉取完整批改（含 diff_html），勿用 essay.correction 覆盖
    const checkInterval = window.setInterval(async () => {
      if (essayStore.currentEssay) {
        await essayStore.fetchEssay(essayStore.currentEssay.id)
        if (
          essayStore.currentEssay.status === 'done' ||
          essayStore.currentEssay.status === 'failed'
        ) {
          window.clearInterval(checkInterval)
        }
      }
    }, 2000)

    // 10秒后停止轮询
    window.setTimeout(() => window.clearInterval(checkInterval), 10000)
  } catch (error: unknown) {
    const msg = (error as Error).message || '提交失败'
    ElMessage.error(msg)
    if (msg.includes('不足') || msg.includes('兑换')) {
      nextTick(() => {
        router.push('/profile?tab=membership')
      })
    }
  }
}

function handleReset() {
  if (essayStore.currentEssay) {
    pendingEditorContent.value = {
      title: essayStore.currentEssay.title ?? '',
      prompt: essayStore.currentEssay.prompt ?? '',
      content_text: essayStore.currentEssay.content_text ?? '',
    }
  }
  essayStore.clearCurrent()
  router.push('/essay/correction').then(() => {
    nextTick(() => {
      if (editorRef.value && pendingEditorContent.value) {
        const p = pendingEditorContent.value
        editorRef.value.form.title = p.title
        editorRef.value.form.prompt = p.prompt
        editorRef.value.form.content_text = p.content_text
        pendingEditorContent.value = null
      }
    })
  })
}

const favoriteLoading = ref(false)
const unfavoriteLoading = ref(false)

async function handleFavorite() {
  if (!essayStore.currentEssay) return
  favoriteLoading.value = true
  try {
    await essayStore.favoriteEssay(essayStore.currentEssay.id)
    essayStore.setCurrentEssayFavorited(true)
    ElMessage.success('收藏成功')
    essayStore.fetchEssay(essayStore.currentEssay.id).catch(() => {})
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '收藏失败')
  } finally {
    favoriteLoading.value = false
  }
}

async function handleUnfavorite() {
  if (!essayStore.currentEssay) return
  unfavoriteLoading.value = true
  try {
    await unfavoriteEssay(essayStore.currentEssay.id)
    essayStore.setCurrentEssayFavorited(false)
    ElMessage.success('已取消收藏')
    essayStore.fetchEssay(essayStore.currentEssay.id).catch(() => {})
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '取消收藏失败')
  } finally {
    unfavoriteLoading.value = false
  }
}

async function handleSaveDraft() {
  if (!authStore.isAuthenticated) {
    ElMessage.warning('请先登录')
    router.push('/login')
    return
  }
  if (!authStore.isMember) {
    showDraftMemberTip.value = true
    return
  }
  if (!editorRef.value) return

  const { form } = editorRef.value
  if (!form.content_text.trim()) {
    ElMessage.warning('请输入作文内容')
    return
  }

  try {
    await essayStore.createDraft({
      title: form.title,
      content_text: form.content_text,
    })
    ElMessage.success('草稿已保存')
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '保存失败')
  }
}

function loadDraft(draft: { title?: string; content_text: string }) {
  if (editorRef.value) {
    editorRef.value.form.title = draft.title || ''
    editorRef.value.form.content_text = draft.content_text
  }
  showDrafts.value = false
}

async function deleteDraft(id: number) {
  try {
    await ElMessageBox.confirm('确定要删除这个草稿吗？', '提示', {
      type: 'warning',
    })
    await essayStore.deleteDraft(id)
    ElMessage.success('删除成功')
  } catch (e) {
    if (e !== 'cancel') {
      ElMessage.error((e as Error).message || '删除失败')
    }
  }
}
</script>

<style scoped>
.correction-page {
  min-height: calc(100vh - var(--header-height) - 80px);
  padding: var(--spacing-lg) 0;
  background: transparent;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 var(--spacing-md);
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header h2 {
  margin: 0;
  font-size: var(--font-size-xl);
  font-weight: 600;
}

.header-actions {
  display: flex;
  gap: var(--spacing-sm);
}

.submit-section {
  margin-top: var(--spacing-lg);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
  align-items: center;
}

.submit-actions {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-md);
  align-items: center;
}
@media (max-width: 768px) {
  .submit-actions {
    flex-direction: column;
    align-items: stretch;
  }
  .submit-actions .el-button {
    width: 100%;
    margin: 0;
  }
}

.result-header {
  display: flex;
  justify-content: flex-end;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-lg);
}

.draft-op-row {
  display: flex;
  flex-wrap: nowrap;
  gap: 4px;
  justify-content: flex-end;
}

/* 草稿列表：空状态不显示表格内置「暂无数据」行，由下方自定义空状态展示 */
.drafts-table :deep(.el-table__empty-block) {
  display: none;
}

.drafts-empty-state {
  padding: var(--spacing-xl) 0;
}

/* 弹窗内段落上边距、左右内边距、链接与正文同一行高 */
.draft-member-tip-p {
  margin-top: var(--spacing-md);
  padding-left: var(--spacing-md);
  padding-right: var(--spacing-md);
}
.draft-member-tip-p .el-link {
  vertical-align: baseline;
}
.draft-member-tip-p .el-link .el-link__inner {
  line-height: inherit;
}
</style>

<!-- 墨滴不足弹窗内「升级会员」链接样式（弹窗 teleport 到 body，需非 scoped） -->
<style>
.app-dialog .ink-shortage-link {
  color: var(--color-primary, #d97706);
  text-decoration: underline;
  font-weight: bold;
  cursor: pointer;
}
</style>
